const context = require("../../context/Java110Context.js");
const constant = context.constant;
export default {
	data() {
		return {
			admin:{},
			communityId:'',
			communityName:'',
			callAdminModal:false
		}
	},
	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad: function(options) {
		let _that = this;
		context.getOwner(function (_ownerInfo) {
		  if (_ownerInfo) {
		    let _active = _ownerInfo.state == '10000' ? 1 : 2;
	
		      _that.communityId= _ownerInfo.communityId;
		      _that.communityName= _ownerInfo.communityName;
			  _that._loadAdmin();
		  }
		});
		
	},
	methods: {
		_loadAdmin:function(){
			let _that = this;
			let _objData = {
				page: 1,
				row: 1,
				communityId: this.communityId,
				memberTypeCd: '390001200000'
			};
			context.request({
				url: constant.url.listStore,
				header: context.getHeaders(),
				method: "GET",
				data: _objData,
				//动态数据
				success: function(res) {
					console.log("请求返回信息：", res);
			
					if (res.statusCode == 200) {
						_that.admin = res.data.stores[0];
						return;
					}
					uni.showToast({
						title: "服务器异常了",
						icon: 'none',
						duration: 2000
					});
				},
				fail: function(e) {
					wx.showToast({
						title: "服务器异常了",
						icon: 'none',
						duration: 2000
					});
				}
			});
		},
		_callPhone:function(){
			this.callAdminModal = true;
		},
		_doCall: function() {
			let _that = this;
			uni.makePhoneCall({
				// 手机号
				phoneNumber: _that.admin.tel,
		
				// 成功回调
				success: (res) => {
					_that.callAdminModal = false;
				},
		
				// 失败回调
				fail: (res) => {
					console.log('调用失败!')
				}
		
			});
		},
		_cancleCall: function() {
			this.callAdminModal = false;
		}
	}
}
